import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const SetTweetsList = 'SET_TWEETS_LIST'
export const tweetsList = 'TWEETS_LIST'
export const SaveTweet = 'SAVE_TWEET'

const validTweetData = (tweetData) => {
  if (tweetData.content === '') throw 'ツイートなし'
  if (tweetData.authorName === '') throw '名前なし'

  return true
}

const convertTweetData = (tweetData) => {
  const createdDateTime = new Date(tweetData.createdDateTime)
  const createdYear = createdDateTime.getFullYear()
  const createdMonth = ('0' + (createdDateTime.getMonth() + 1)).slice(-2)
  const createdDate = ('0' + createdDateTime.getDate()).slice(-2)
  const createdHour = createdDateTime.getHours()
  const createdMinute = createdDateTime.getMinutes()

  return {
    content: tweetData.content,
    authorName: tweetData.authorName,
    createdDateTime: `${createdYear}/${createdMonth}/${createdDate} ${createdHour}:${createdMinute}`
  }
}

export default new Vuex.Store({
  state: {
    tweetsList: [
      {
        id: 1,
        content: '既に入力されたツイート',
        authorName: 'ホゲ山 フガ男',
        createdDateTime: '2018/10/04 09:10'
      },
    ]
  },
  mutations: {
    [SetTweetsList] ({ tweetsList }, payload) {
      tweetsList = payload
    }
  },
  getters: {
    [tweetsList]: state => state.tweetsList
  },
  actions: {
    [SaveTweet] ({ state, commit }, payload) {
      try {
        validTweetData(payload)
      }
      catch (error) {
        console.log(error)
        return
      }

      const tweetsList = state.tweetsList
      const currentNumberOfTweets = tweetsList.length
      const tweet = convertTweetData(payload)

      tweet.id = currentNumberOfTweets + 1

      // 本来ならここで DB 保存
      // 保存が成功したら最新のリストを取得
      tweetsList.unshift(tweet)

      commit(SetTweetsList, tweetsList)
    }
  }
})
